import { MD5 } from 'crypto-js'

import type { UserInfo } from './user'

import { Http } from '@/utils/http'

interface LoginRequest {
    /** 用户名 */
    userName: string
    /** 密码 */
    password: string
    /** 验证码 */
    verifyCode?: string
    /** 记住密码 */
    remember?: boolean
}

interface LoginResult {
    userInfo: UserInfo
    roleIdList: number[]
    rights: string
    userId: number
    token: string
}

class LoginController {
    /** 获取验证码 */
    getVerifyCode() {
        return Http.GET<Blob>('/login/getVerifyCode', { responseType: 'blob' })
    }

    loginRequest: LoginRequest = reactive({
        userName: 'test',
        password: '12345678',
        verifyCode: '',
        remember: false,
    })

    /** 登录 */
    login(loginRequest: LoginRequest) {
        const data = { ...loginRequest, password: MD5(loginRequest.password).toString() }
        return Http.POST<LoginResult, LoginRequest>('/login/login', data)
    }
}
const LoginApi = new LoginController()
export { LoginController, LoginApi }
